body{
    background-color: #2d162c;
    color: #412752;
    font-family: 'Press Start 2P', cursive
}

.titu{
    text-align: center;

}

.di{
    margin-top: 40px;
    margin-left: 400px;
    background-color: #9775a6; 
    height: 590px;
    width: 450px;
    border-radius: 10px;
}


label{
    margin-left: 100px;
}

input{
     
    background-color: #9775a6; 
    margin-left: 100px;
    height: 30px;
    width: 230px;
    border-left: none;
    border-right: none;
    border-top: none; 
    border-bottom-color:#2d162c;
    
}

input[type="text"]::placeholder {
    color: #683a68;
    font-family: 'Press Start 2P', cursive
}

button{
    margin-left: 130px;
